.App {
  display: flex;
  flex-direction: column;
}
.catalog {
  margin: 20px;
  display: grid;
   grid-template-columns:  repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem
}
.card-action {
  display: flex;
  justify-content: center;
}
.btn {
  text-align: center;
}
.card-title{
  text-align: center;
}
.goBackLine {
  display: flex;
  justify-content: center;
  margin: 10px;
}
.recipe {
  width: 600px;
  margin: 20px auto;
}
img, iframe {
  max-width: 100%;
}
iframe {
  width: 600px;
  height: 450px;
  cursor: pointer;
}
.tableDouble{
  display: flex;
}
#search-field {
  width: 400px;
}
.input-field {
  display: flex;
}